import React, { Component } from 'react';

import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom'

import AllGroupsTabContent from './components/AllGroupsTabContent';
import GroupTabContent from './components/GroupTabContent';
import GroupSwitch from './components/GroupSwitch';

class App extends Component {

  constructor() {
    super();
    // 
    let todos = [
      { text: '', groupid: 'living' },
      { text: '', groupid: 'living' },
      { text: '', groupid: 'living' },
      { text: '', groupid: 'work' },
      { text: '', groupid: 'work' },
      { text: '', groupid: 'work' },
      { text: '', groupid: 'family' },
      { text: 'React', groupid:'Leaning'}
    ];
    // 
    let groups = [
      { name: '', id: undefined },
      { name: '', id: 'living' },
      { name: '', id: 'work' },
      { name: '', id: 'family' },
      { name: '', id: 'Leaning'}
    ];

    this.state = {
      todos: todos,
      groups: groups,
      // 
      activeGroupId: undefined,
    }
  }

  createTodo(newtodotext, groupid) {

    const newtodo = {
      text: newtodotext,
      groupid: groupid || this.state.activeGroupId
    }

    this.setState({
      todos: [...this.state.todos, newtodo]
    })
  }
  removeTodo(removingtodo) {
    const newtodos = this.state.todos.filter((todo, index) => {
      return !(todo.groupid == removingtodo.groupid &&
        removingtodo.text === todo.text)
    });
    this.setState({
      todos: newtodos
    })
  }
  switchGroup(groupid) {
    this.setState({
      activeGroupId: groupid
    })
  }
  render() {
    return (
      <Router>
        <div>
          <Route exact path="/"
            render={() => {
              return (
                <div>
                  <GroupSwitch
                    groups={this.state.groups}
                    activeGroupId={undefined}
                    switchGroup={(groupid) => this.switchGroup(groupid)}
                  />
                  <AllGroupsTabContent
                    todos={this.state.todos}
                    removeTodo={(todo) => this.removeTodo(todo)}
                  />
                </div>
              );

            }}
          />
          <Route exact path="/group/:id"
            render={({ match }) => {
              const todos = this.state.todos.filter((todo, index) => {
                return todo.groupid === match.params.id;
              })
              return (
                <div>
                  <GroupSwitch
                    groups={this.state.groups}
                    activeGroupId={match.params.id}
                    switchGroup={(groupid) => this.switchGroup(groupid)}
                  />
                  <GroupTabContent
                    todos={todos}
                    createTodo={(newtodo, groupid) => this.createTodo(newtodo, groupid)}
                    removeTodo={(todo) => this.removeTodo(todo)}
                  />
                </div>
              );
            }}
          />

        </div>

      </Router>
    );
  }
}

export default App;



