import React, { Component } from 'react';

class TodoInput extends Component {
  render() {
      return <input 
        type="text" 
        placeholder="..."    
        value={this.props.inputText}
        onChange={this.props.udpateInputText}    
      />;
  }
}

export default TodoInput;



